<template>
  <div class="home">
    <section class="hero is-medium is-dark mb-6">
        <div class="hero-body has-text-centered">
           <p class="title mb-6">
              Welcome to Keski Flowers
           </p>
           <p class="subtitle">
              create the emotions, feel the blossom
           </p>
        </div>
    </section>

    <div class="columns is-multiline">
      <div class="column is-12">
        <h2 class="is-size-2 has-text-centered">Latest Flowers</h2>
      </div>

      <FlowerBox
        v-for="flower in latestFlowers"
        v-bind:key="flower.flower_id"
        v-bind:flower="flower"
      />

    </div>

  </div>
</template>

<script>

import axios from 'axios'
import FlowerBox from '@/components/FlowerBox'

export default {
  name: 'Home',
  data(){
    return{
      latestFlowers:[]
    }
  },
  components: {
    FlowerBox
  },
  mounted(){
    this.getLatestFlowers()
    document.title = 'Home | KeskiFlowers'
  },
  methods:{
    async getLatestFlowers(){
      this.$store.commit('setIsLoading', true)
      await axios
        .get('/api/v1/latest-flowers/')
        .then(response =>{
           this.latestFlowers = response.data
        })
        .catch(error => {
          console.log(error)
        })
      this.$store.commit('setIsLoading', false)
    }
  }
}
</script>


