<template>

<div class="page-flower">
   <div class="columns is-multiline">
      <div class="column is-9">
         <figure class="image mb-6">
            <img v-bind:src="flower.get_image">
         </figure>

         <h1 class="title">{{flower.flower_name}}</h1>
         <p>{{flower.flower_description}}</p>

      </div>

      <div class="column is-3">
         <h2 class="subtitle">Information</h2>
         <p><strong>Price: </strong>Ksh{{flower.flower_price}}</p>

         <div class="field has-addons mt-6">
            <div class="control">
               <input type="number" class="input" min="1" v-model="quantity">
            </div>

            <div class="control">
               <a class="button is-dark" @click="addToCart">Add to cart</a>
            </div>

         </div>

      </div>

   </div>
</div>

</template>

<script>

import axios from 'axios'
import {toast} from 'bulma-toast'

export default{
   name: 'Flower',
   data(){
      return{
         flower: {},
         quantity: 1
      }
   },

   mounted(){
      this.getFlower()
   },
   methods: {
      async getFlower(){
         this.$store.commit('setIsLoading', true)

         const category_slug=this.$route.params.category_slug
         const flower_slug=this.$route.params.flower_slug

         await axios
           .get(`/api/v1/flowers/${category_slug}/${flower_slug}`)
           .then(response => {
             this.flower = response.data

             document.title = this.flower.flower_name + ' | KeskiFlowers'

           })
           .catch(error => {
             console.log(error)
           })
         this.$store.commit('setIsLoading', false)
      },
      addToCart() {
        if (isNaN(this.quantity) || this.quantity < 1){
          this.quantity = 1
        }

        const item = {
          flower: this.flower,
          quantity: this.quantity
        }

        this.$store.commit('addToCart', item)

        toast({
          message: 'The product has been added to the cart',
          type: 'is-success',
          dismissible: true,
          pauseOnHover: true,
          duration: 2000,
          position: 'bottom-right'
        })

      }
   }
}

</script>