<template>
  <div class="page-category">
    <div class="columns is-multiline">
      <div class="column is-12">
        <h1 class="is-size-2 has-text-centered">{{category.name}}</h1>
      </div>

      <FlowerBox
        v-for="flower in category.flowers"
        v-bind:key="flower.flower_id"
        v-bind:flower="flower"
      />
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import {toast} from 'bulma-toast'
import FlowerBox from '@/components/FlowerBox'

export default{
  name: 'Category',
  components:{
    FlowerBox
  },
  data(){
    return{
      category: {
        flowers: []
      }
    }
  },
  mounted(){
    this.getCategory()
  },
  watch: {
    $route(to, from){
      if (to.name === 'Category'){
        this.getCategory()
      }
    }
  },
  methods:{
    async getCategory(){
      const categorySlug = this.$route.params.category_slug

      this.$store.commit('setIsLoading', true)

      axios
        .get(`api/v1/flowers/${categorySlug}/`)
        .then(response => {
          this.category = response.data

          document.title = this.category.name + ' |KeskiFlowers'
        })
        .catch(error => {
          console.log(error)

          toast({
            message: 'Something went wrong. Please try again',
            type: 'is-danger',
            dismissible: true,
            pauseOnHover: true,
            duration: 2000,
            position: bottom-right,
          })
        })

      this.$store.commit('setIsLoading', false)
    }
  }
}
</script>